import * as React from 'react';
import { useMachine } from '@xstate/react';
import { createMachine, assign } from 'xstate';

const initialTodos = [
	{ id: 1, text: 'Buy milk', completed: false },
	{ id: 2, text: 'Buy eggs', completed: false },
];

export default function Index() {
	console.log('Render Index');

	return (
		<ServiceProvider>
			<div>
				<h1>Todo App</h1>
				<TodosList />
				<AddTodoForm />
			</div>
		</ServiceProvider>
	);
}

const TodosList = React.memo(() => {
	const [state] = React.useContext(serviceContext)!;
	const todos = state.context.todos;
	console.log('Render TodosList');

	return (
		<>
			{todos.map(todo => (
				<TodoItem key={todo.id} todo={todo} />
			))}
		</>
	);
});

const AddTodoForm = React.memo(() => {
	const [, send] = React.useContext(serviceContext)!;

	React.useEffect(() => {
		send({ type: 'SET_TODOS', todos: initialTodos });
	}, [send]);

	function addTodo(text: string) {
		send({ type: 'ADD_TODO', text });
	}

	console.log('Render AddTodoForm');

	return (
		<form
			onSubmit={e => {
				e.preventDefault();
				const text = e.currentTarget.text.value;
				addTodo(text);
			}}
		>
			<input type="text" name="text" />
			<button type="submit">Add Todo</button>
		</form>
	);
});

type Todo = { id: number; text: string; completed: boolean };
type MachineContext = { todos: Array<Todo> };
type EventTypes =
	| { type: 'ADD_TODO'; text: Todo['text'] }
	| { type: 'SET_TODOS'; todos: Array<Todo> }
	| { type: 'REMOVE_TODO'; id: Todo['id'] }
	| { type: 'UPDATE_TODO'; todo: Todo };

const todoMachine = createMachine<MachineContext, EventTypes>({
	id: 'todo',
	predictableActionArguments: true,
	initial: 'editing',
	context: { todos: [] },
	states: {
		editing: {
			on: {
				ADD_TODO: {
					actions: assign({
						todos: ({ todos }, { text }) => {
							return todos.concat({ id: todos.length + 1, text, completed: false });
						},
					}),
				},
				SET_TODOS: { actions: assign({ todos: (_, { todos }) => todos }) },
				REMOVE_TODO: {
					actions: assign({
						todos: ({ todos }, { id }) => todos.filter(todo => todo.id !== id),
					}),
				},
				UPDATE_TODO: {
					actions: assign({
						todos: ({ todos }, { todo }) => {
							const index = todos.findIndex(t => t.id === todo.id);
							const newTodos = [...todos];
							newTodos[index] = todo;
							return newTodos;
						},
					}),
				},
			},
		},
		working: {},
	},
});

type ContextType = ReturnType<typeof useMachine<typeof todoMachine>>;
const serviceContext = React.createContext<ContextType | null>(null);

function ServiceProvider({ children }: { children: React.ReactNode }) {
	const machine = useMachine(todoMachine);
	const context = React.useMemo(() => machine, [machine]);
	return <serviceContext.Provider value={context}>{children}</serviceContext.Provider>;
}

const TodoItem = React.memo(({ todo }: { todo: Todo }) => {
	const [, send] = React.useContext(serviceContext)!;

	console.log('Render TodoItem');

	return (
		<div key={todo.id}>
			<input
				type="text"
				name="text"
				value={todo.text}
				onChange={e => {
					const text = e.currentTarget.value;
					send({ type: 'UPDATE_TODO', todo: { ...todo, text } });
				}}
			/>
			{/* <button onClick={() => removeTodo(todo.id)}>Delete</button> */}
		</div>
	);
});
